import React from 'react';

import ContainerDimensions from 'react-container-dimensions';
import { VictoryChart, VictoryTheme, VictoryAxis, VictoryLine } from 'victory';

const sampleData = [{ x: 1, y: 1 }, { x: 2, y: 2 }, { x: 3, y: 3 }, { x: 4, y: 4 }];

const GenericChart = () => (
  <ContainerDimensions>
    {({ width }) => (
      <VictoryChart width={width} theme={VictoryTheme.material}>
        <VictoryLine data={sampleData} />
        <VictoryAxis />
      </VictoryChart>
    )}
  </ContainerDimensions>
);

export default GenericChart;
